<template lang="pug">
.tooltip-wrapper.position-relative
  slot
    i.fa.fa-question-circle
  .tooltip(:class='tooltipClass')
    .arrow
    .tooltip-inner(:style='{"max-width": maxWidth}') {{ content }}
</template>

<script>
export default {
  name: 'tooltip',
  props: {
    direction: { // tooltip出现的位置/方向，默认显示在上方，可选：top／left／right／bottom
      type: String,
      default: 'top'
    },
    content: { // 文本内容
      type: String,
      required: true
    },
    maxWidth: { // 文本最大宽度
      type: String,
      default: '20rem'
    }
  },
  computed: {
    tooltipClass () { // 从 direction 参数中解析 tooltip 出现的方向
      let classArr = this.direction.split('-')
      classArr[0] = 'bs-tooltip-' + classArr[0]
      return classArr.join(' ')
    }
  }
}
</script>

<style scoped lang="scss">
.tooltip {
  display: none;
  /*给 tooltip 加上宽度后 tooltip-inner 中的 max-width 才能生效*/
  width: 20rem;
  line-height: .875rem;
  .tooltip-inner {
    text-align: left;
    box-shadow: 0 2px 4px 0 rgba(226, 231, 232, 0.5);
  }
}
/*水平方向的定位通用样式*/
%tooltip-horizontal-position {
  top: 50%;
  .arrow {
    top: 50%;
    transform: translateY(-50%);
  }
}
/*垂直方向的定位通用样式*/
%tooltip-vertical-position {
  left: 50%;
  display: flex;
  justify-content: center;
}

/*根据hover的状态控制气泡弹出框的显示与隐藏*/
.tooltip-wrapper:hover > .tooltip {
  opacity: 0.8;
  display: block;
  &.bs-tooltip-left {
    @extend %tooltip-horizontal-position;
    transform: translate(-100%, -50%);
    .tooltip-inner {
      float: right;
    }
  }
  &.bs-tooltip-right {
    @extend %tooltip-horizontal-position;
    left: 100%;
    transform: translateY(-50%);
    .tooltip-inner {
      float: left;
    }
  }
  &.bs-tooltip-top {
    @extend %tooltip-vertical-position;
    top: 0;
    transform: translate(-50%, -100%);
  }
  &.bs-tooltip-bottom {
    @extend %tooltip-vertical-position;
    transform: translateX(-50%);
  }
}
</style>
