<template lang="pug">
.wrapper.d-inline-block.w-100
  .tags-wrap.form-control(ref='wrapper')
    span.tag(ref='tag', v-for='(tag,index) in tagsList')
      span {{ tag }}
        i.tag-close.cf-close.cursor-pointer.ml-2(title='删除关键词', @click='removeTag(index)')
    input(v-model.trim='tagName', :placeholder='placeholder', @keyup.enter='addTags', @keyup.delete='removeTags')
</template>

<script>
export default {
  name: 'cf-tags',
  props: {
    setTagsList: Array, // tag列表
    placeholder: {
      type: String,
      default: '使用Enter键隔开每个关键词'
    }
  },
  data () {
    return {
      tagName: '', // 当前输入的tag名称
      tagsList: [], // tag列表
      widthData: '100%'
    }
  },
  watch: {
    'setTagsList': 'setTags', // 监控父组件的tag数组值
    'tagsList': 'handleTags' // 监控子组件的tag数组值，发送给父组件
  },
  methods: {
    setTags () {
      this.tagsList = this.setTagsList
    },
    addTags () {
      if(this.tagName) {
        this.tagsList.push(this.tagName)
        this.tagName = ''
      } else {
        return
      }
    },
    removeTag (index) { // 移除单个tag
      this.tagsList.splice(index,1)
    },
    removeTags () { // 通过delete键批量删除tag
      this.tagsList.splice(this.tagsList.length - 1,1)
    },
    handleTags () {
      this.$emit('submitTags', this.tagsList)
    },
  }
}
</script>

<style scoped>
.tags-wrap {
  width: 100%;
  min-height: calc(2.25rem + 2px);
  height: auto;
  padding: .375rem .75rem 0 .75rem;
}
.tag {
  display: inline-block;
  margin-bottom: .375rem;
  padding: 0 5px 0 10px;
  margin-right: 15px;
  border: 1px solid #5cb85c;
  border-radius: .25rem;
  background: #e9f8b8;
}
.tag span {
  width: 100%;
}
.tag .tag-close {
  color: #5cb85c;
  cursor: pointer;
}
.tag-close::before {
  content: "\2716";
  font-style: normal;
}
.tags-wrap input {
  min-width: 50%;
  margin-bottom: .375rem;
  border: none;
  outline: none;
}
</style>
