<template lang="pug">
  .cf-cascader
    .cf-input
      input(
        type='text'
        v-model='inputValue'
        :placeholder='placeholder'
        :disabled="disabled"
        :readonly="readonly"
        @input="debouncedInputChange"
        @click.prevent.stop='toggleMenu'
      )
      span.cf-input__suffix
        i.down-arrow
    cf-cascader-menu(
      ref='menu'
      :label='label'
      :visible='visible'
      :disabled='disabled'
      :hasChildren='hasChildren'
      :fetchData='fetchData'
      @select='select'
    )
</template>

<script>
import CfCascaderMenu from './Menu'
import { debounce } from 'throttle-debounce'

export default {
  name: 'cf-cascader',
  components: { CfCascaderMenu },
  props: {
    value: {
      type: Array,
      default: () => ([])
    },
    options: {
      type: Array,
      required: true,
      default: () => ([])
    },
    label: {
      type: String,
      default: 'label'
    },
    separator: {
      type: String,
      default: '/'
    },
    debounce: {
      type: Number,
      default: 300,
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    disabled: Boolean,
    hasChildren: {
      type: Function,
      default: item => item.children
    },
    fetchData: {
      type: Function,
      default: () => (() => {})
    },
    searchable: Boolean,
    serverSideSearch: {
      type: Function,
      default: () => (() => {})
    },
  },
  data () {
    return {
      inputValue: '',
      visible: false, // Menu是否可见，默认隐藏
      isRequesting: false, // 是否正在请求
      debouncedInputChange() {},
    }
  },
  watch: {
    value () {
      this.inputValue =  this.value.reduce((res, curCategory) => {
        res.push(curCategory[this.label])
        return res
      }, []).join(` ${this.separator} `)
    }
  },
  computed: {
    readonly () {
      return !this.searchable
    }
  },
  methods: {
    handleInputChange (value) {
      this.serverSideSearch(value).then(options => {
        this.$refs.menu.options = options
        this.$refs.menu.setSelected([])
        this.visible = true
      })
    },
    toggleMenu () {
      if (!this.visible) {
        this.$refs.menu.options = this.options
        this.$refs.menu.setSelected(this.value)
        this.visible = true
      } else {
        this.visible = false
      }
    },
    /**
     * 选中数据项
     * @param {Array} selected - 当前选中的值
     */
    select (selected) {
      this.$emit('input', selected)
      this.visible = false
    },
  },
  created() {
    this.debouncedInputChange = debounce(this.debounce, value => {
      this.handleInputChange(value)
    })
  },
}
</script>
