<template lang="pug">
.wrapper.spinner
  spinner.outline-primary(v-show="isUploading")
  label.upload-label.w-100.h-100
    input.d-none(type='file' :accept='acceptFileType' @change="setUploadFile" :disabled='isUploading')
    .d-flex.flex-column.justify-content-center.align-items-center.h-100.font-weight-normal
      .iconfont.icon-plus-circle
      .upload-text {{ tipText }}
      slot
</template>

<script>
// 选择文件上传的区域
import Spinner from './Spinner'

export default {
  name: 'select-file',
  props: {
    acceptFileType: { // 可以选择的文件类型，默认都可选
      type: String,
      default: '*'
    },
    tipText: { // 提示文案
      type: String,
      default: 'Upload'
    },
    isUploading: { // 正在上传标志
      type: Boolean,
      default: false
    }
  },
  components: {
    Spinner
  },
  methods: {
    setUploadFile (e) {
      let file = e.target.files[0]
      if (file.name) { // 如果有选取文件的话抛出事件给父组件
        this.$emit('set-upload-file', file)
        e.target.value = '' // 重置，防止多次选择相同文件时不触发change事件
      }
    }
  }
}
</script>

<style scoped lang="scss">
.wrapper {
  border-radius: .25rem;
  border: 1px dashed rgba(241,84,64,1);
  .upload-label {
    cursor: pointer;
    .icon-plus-circle {
      line-height: 1.5rem;
      font-size: 1.5rem;
      color: rgba(241,84,64,1);
    }
    .upload-text {
      color: #999999;
    }
  }
}
</style>
