<template lang="pug">
ul.nav.nav-tabs
  li.nav-item(v-for='(tab, index) in tabs')
    a.nav-link.mb-0.text-capitalize(:class='{ "active": isActive(index) }' href='#'
      @click.prevent='setActive(index)'
    ) {{ tab.text }}
    span.highlight
</template>

<script>
export default {
  name: 'cf-nav-tabs',
  props: {
    tabs: { // 导航tab配置 - [{text文案,...}]，组件内只关注显示，在父组件处理业务逻辑
      type: Array,
      required: true
    },
    activeIndex: { // 当前 active 的tab索引，默认为配置列表第一项
      type: Number,
      default: 0
    }
  },
  methods: {
    /**
     * 当前tab是否active
     * @param {number} index - 当前tab在配置列表的索引
     */
    isActive (index) {
      return index === this.activeIndex
    },
    /**
     * 设置当前tab为active，并向父组件传递tab改变事件
     * @param {number} to - 当前tab在配置列表的索引
     */
    setActive (to) {
      this.$emit('update:activeIndex', to)
    }
  }
}
</script>
