<template lang="pug">
div(:class='{ "modal-open": isShown }')
  .modal.fade(:class='modalClass')
    .modal-dialog.modal-dialog-centered(:class='[modalType]')
      .modal-content
        .modal-header
          h5.modal-title {{ title }}
          button.close(type='button' @click='hide')
            span &times;
        .modal-body
            form#prompt-box-form(@submit.prevent='onSubmit')
              slot(name='body')
        .modal-footer
          button.btn.btn-outline-primary(type='button', @click='hide') {{ notConfirmText }}
          button.btn.btn-primary(type='submit' form='prompt-box-form') {{ confirmText }}
  .modal-backdrop.fade(v-show='isShown' :class='{ "show": isShown }')
</template>

<script>
import addBodyOverflow from './utils'

export default {
  name: 'cf-prompt-box',
  props: {
    modalType: { // modal大小可通过 .modal-sm .modal-md .modal-lg 样式来控制
      type: String,
      default: 'modal-sm'
    },
    notConfirmText:  { // “不确认”按钮文案
      type: String,
      default: '取消'
    },
    confirmText: { // “确认”按钮文案
      type: String,
      default: '确定'
    },
  },
  data () {
    return {
      isShown: false, // 默认隐藏
      title: '标题名称', // 标题
      callback: null, // 提交“确认”请求成功的回调
    }
  },
  computed: {
    modalClass () {
      return {
        'show': this.isShown,
        'd-block': this.isShown
      }
    }
  },
  methods: {
    hide () {
      this.isShown = false
      addBodyOverflow(false)
    },
    /**
     * 打开弹窗
     * @param {string} title - 标题
     * @param {string} msg - 消息内容
     */
    show (title, callback = null) {
      this.title = title
      this.callback = callback
      this.isShown = true
      addBodyOverflow(true)
    },
    // 传递confirm事件，在父组件处理业务逻辑
    onSubmit () {
      this.$emit('confirm', () => {
        this.hide()
        this.callback && this.callback()
      })
    },
  }
}
</script>
