<template lang="pug">
div(:class='{ "modal-open": isShown }')
  .modal.fade(:class='modalClass')
    .modal-dialog.modal-dialog-centered(:class='[modalType]')
      .modal-content
        .modal-header
          h5.modal-title {{ title }}
          button.close(type='button' @click='hide')
            span &times;
        .modal-body
          p {{ message }}
        .modal-footer
          button.btn.btn-primary(type='button', @click='hide') {{ confirmText }}
  .modal-backdrop.fade(v-show='isShown' :class='{ "show": isShown }')
</template>

<script>
import addBodyOverflow from './utils'

export default {
  name: 'cf-alert-box',
  props: {
    modalType: { // modal大小可通过 .modal-sm .modal-md .modal-lg 样式来控制
      type: String,
      default: 'modal-sm'
    },
    confirmText: { // 确认按钮文案
      type: String,
      default: '确定'
    },
  },
  data () {
    return {
      isShown: false, // 默认隐藏
      title: '标题名称', // 标题
      message: '消息内容' // 消息内容
    }
  },
  computed: {
    modalClass () {
      return {
        'show': this.isShown,
        'd-block': this.isShown
      }
    }
  },
  methods: {
    hide () {
      this.isShown = false
      addBodyOverflow(false)
    },
    /**
     * 打开弹窗
     * @param {string} title - 标题
     * @param {string} msg - 消息内容
     */
    show (title, msg) {
      this.isShown = true
      this.title = title
      this.message = msg
      addBodyOverflow(true)
    }
  }
}
</script>
