<template lang="pug">
  .cf-cascader-menus(:class="{ show: visible && !disabled }")
    ul.cf-cascader-menu(v-for='(lists, menuIndex) in activeMenuList')
      template(v-if='lists.length')
        li.cf-cascader-menu__item(v-for='(item, index) in lists' :key='menuIndex + "-" + index'
          :class='{ "active": isActive(item)}'
          @mouseenter='activeItem(item, menuIndex)'
          @click.prevent='select(item)')
          span {{ item[label] }}
          i.right-arrow(v-if='hasChildren(item)')
      template(v-else)
        li.cf-cascader-menu__item
          span 加载中...
</template>

<script>
/**
 * 浅拷贝
 * @param {array} arr - 拷贝的数组
 */
const copyArr = arr => arr.map(_ => _)

export default {
  name: 'CfCascaderMenu',
  props: {
    label: String,
    visible: Boolean,
    disabled: Boolean,
    hasChildren: {
      type: Function,
      default: item => item.children
    },
    fetchData: {
      type: Function,
      default: () => (() => {})
    },
  },
  data () {
    return {
      options: [],
      selected: [], // 当前选中的值
    }
  },
  computed: {
    activeMenuList () { // 激活的菜单列表
      /**
       * 递归构建activeMenuList，N从0开始
       * @param {array} options - 第N层菜单
       * @param {array} activeMenuList - 已激活的菜单列表，N=0时为空数组
       */
      const loadActiveTree = (options, activeMenuList = []) => {
        const level = activeMenuList.length; // N(level)为已激活的菜单列表长度
        activeMenuList[level] = options; // 加入菜单列表
        let activeCategory = this.selected[level] ? options.find(_ => _.value === this.selected[level].value) : ''; // 第N个选中的选项
        if (!!activeCategory && this.hasChildren(activeCategory)) { // 如果第N个选中的选项被定义且非叶子节点，则递归求第N+1个菜单
          if (activeCategory.children.length === 0) {
            this.fetchData(activeCategory).then(() => {
              loadActiveTree(activeCategory.children, activeMenuList)
            })
          } else {
            loadActiveTree(activeCategory.children, activeMenuList)
          }
        }
        return activeMenuList;
      }

      return loadActiveTree(this.options)
    },
  },
  methods: {
    setSelected (array) {
      this.selected = copyArr(array)
    },
    /**
     * 判断当前选项是否已选中
     * @param {object} item - 当前选项
     */
    isActive (item) {
      return this.selected.find(_ => _.value === item.value)
    },
    /**
     * 激活数据项
     * @param {object} item - 激活的数据项
     * @param {number} menuIndex - 所属Menu序号
     */
    activeItem (item, menuIndex) {
      const len = this.activeMenuList.length; // len > menuIndex, 确保删除掉 selected 中 menuIndex后所有的项
      this.selected.splice(menuIndex, len, item)
      if (this.hasChildren(item)) { // 如果不是根节点且children是null或undefined，获取并更新类目树
        this.fetchData(item)
      }
    },
    /**
     * 选中数据项
     * @param {object} item - 选中的数据项
     */
    select (item) {
      if (!this.hasChildren(item)) {
        if (item.isSearchOption) {
          this.$emit('select', item.value)
        } else {
          this.$emit('select', copyArr(this.selected))
        }
      }
    },
  }
}
</script>
