/**
 * 显示/隐藏弹窗时，需要在body元素上添加/移除 body-overflow-hidden 样式
 * @param {Boolean} toggle - 是否添加样式
 */
export default function addBodyOverflow (toggle) {
  if (toggle) {
    // 添加 .body-overflow-hidden 样式
    document.body.classList.add('body-overflow-hidden')
  } else {
    // 移除 .body-overflow-hidden 样式
    document.body.classList.remove('body-overflow-hidden')
  }
}