import emitter from '../mixins/emitter'

const draggableItem = {
  name: 'draggable-item',
  componentName: 'draggable-item',
  mixins: [emitter],
  props: {
    tag: { // 包裹元素
      type: String,
      default: 'div'
    }
  },
  data () {
    return {
      draggableFlag: true
    }
  },
  render(h) {
    return h(this.tag, {
      domProps: {
        draggable: this.draggableFlag
      },
      on: {
        dragstart: this.handleDragStart,
        dragover: this.handleDragOver,
        dragend: this.handleDragEnd,
        drop: this.handleDrop
      }
    }, this.$slots.default)
  },
  methods: {
    handleDragStart(event) {
      event.stopPropagation()
      event.dataTransfer.effectAllowed = 'move'

      // wrap in try catch to address IE's error when first param is 'text/plain'
      try {
        // setData is required for draggable to work in FireFox
        // the content has to be '' so dragging a item out of the list won't open a new tab in FireFox
        event.dataTransfer.setData('text/plain', '')
      } catch (e) {
      }

      // this.$emit('draggable-item-start', event.target)
      this.dispatch('draggable', 'draggable-item-start', event)
    },
    handleDragOver(event) {
      event.stopPropagation()
      event.preventDefault()
      event.dataTransfer.dropEffect = 'move'
    },
    handleDragEnd(event) {
      event.stopPropagation()
      event.preventDefault()
      event.dataTransfer.dropEffect = 'move'

      this.$emit('drag-end', event.target)
    },
    handleDrop(event) {
      event.stopPropagation()
      event.preventDefault()

      this.dispatch('draggable', 'draggable-item-drop', event)
    },
    changeDraggableFlag (data) {
      this.draggableFlag = data
    }
  },
  created () {
    this.$on('changed-draggable-flag', this.changeDraggableFlag)
  }
}

export default draggableItem