<template lang="pug">
div
  .spinner-wrapper.d-flex.justify-content-center.align-items-center
    .spinner-circle(:style='styleObject')
</template>

<script>
// 加载进度圆环

export default {
  name: 'app-spinner',
  props: {
    size: { // 大小，默认为根元素字体大小
      type: Number,
      default: 16
    },
    borderWidth: { // 粗细
      type: Number,
      default: 2
    }
  },
  computed: {
    styleObject () {
      return {
        borderWidth: this.borderWidth +'px',
        width: this.size +'px',
        height: this.size +'px'
      }
    }
  }
}
</script>

<style lang="scss">
/*必须在父组件上添加该class*/
.spinner {
  position: relative;
}
.spinner-wrapper {
  position: absolute;
  top: 1px;
  bottom: 1px;
  left: 1px;
  right: 1px;
  .outline-primary & {
    background-color: white;
    &>.spinner-circle {
      border-right-color: rgba(241,84,64,1);
      border-bottom-color: rgba(241,84,64,1);
    }
  }
}
.spinner-circle {
  animation: a-spin .7s linear infinite;
  border-radius: 50%;
  border-style: solid;
  border-color: transparent;
}
@keyframes a-spin {
  0% {
    transform: rotate(0deg);
   }
  100% {
    transform: rotate(1turn);
  }
}
</style>
