<template lang="pug">
nav(v-show='total > 0')
  .d-inline-block.mr-3.align-top
    span 显示第 {{ currentStart }} 到第 {{ currentEnd }} 条数据，每页显示 
    select.custom-select.w-auto(v-if='pageList && pageList.length > 0' v-model='selectedPage' @change='selectPage')
      option(v-for='item in pageList' :value='item') {{ item }}
    span(v-else) {{ itemsPerPage }} 
    span 条，共 {{ total }} 条
  .d-inline-block
    ul.pagination(v-if='maxPage > 1')
      li.page-item
        a.page-link(@click.prevent='setPage(1)') &laquo; 首页
      template(v-if='maxPage > 5')
        template(v-if='hasDots')
          li.page-item(v-if='hasPrev')
            a.page-link(@click.prevent='setPage(pageNo - 1)') {{ pageNo - 1 }}
          li.page-item.active
            a.page-link(href='#', @click.prevent='setPage(pageNo)') {{ pageNo }}
          li.page-item
            a.page-link(@click.prevent='setPage(pageNo + 1)') {{ pageNo + 1 }}
          li.page-item
            a.page-link(@click.prevent='setPage(pageNo + 2)') {{ pageNo + 2 }}
          li.page-item.disabled
            a.page-link(href='#') ...
          li.page-item
            a.page-link(@click.prevent='setPage(maxPage)') {{ maxPage }}
        template(v-else)
          li.page-item(
            v-for='n in pageNoArray'
            ':class'=' n === pageNo ? "active" : "" '
          )
            a.page-link(href='#', @click.prevent='setPage(n)') {{ n }}
      template(v-else)
        template(v-for='n in maxPage')
          li.page-item(:class=' n === pageNo ? "active" : "" ')
            a.page-link(href='#', @click.prevent='setPage(n)') {{ n }}
</template>

<script>
export default {
  name: 'cf-pagination',
  props: {
    page: { // 当前页码
      type: Number,
      default: 1
    },
    total: { // 数据总个数
      type: Number,
      default: 1
    },
    itemsPerPage: { // 每页显示多少条数据
      type: Number,
      default: 15
    },
    pageList: { // 由每页显示多少条数据选项组成的数组
      type: Array,
      default: () => [15, 25, 50, 100]
    },
  },
  data () {
    return {
      selectedPage: this.itemsPerPage // 默认下拉展示多少条数据
    }
  },
  watch: {
    itemsPerPage (val) {
      this.selectedPage = val
    }
  },
  computed: {
    pageNo () { // 当前页数
      return Number(this.page) || 1
    },
    maxPage () { // 最大页数
      return Math.ceil(this.total / this.itemsPerPage)
    },
    hasDots () { // 最大页数和当前页数的间隔大于3时，显示点点点
      return this.maxPage - this.pageNo > 3
    },
    hasPrev () { // 有前一页
      return this.pageNo > 1
    },
    pageNoArray () { // 当页数大于5时，最大页数和当前页间隔小于等于3
      var count = this.maxPage
      return new Array(count-4, count-3, count-2, count-1, count)
    },
    hasRemainder () { // 检索结果总数除以每页显示个数是否有余数
      return (this.total % this.itemsPerPage) > 0
    },
    currentStart () { // 当前页面展示数据（在总个数中的）起始位置
      return this.itemsPerPage * (this.pageNo - 1) + 1
    },
    currentEnd () { // 当前页面展示数据（在总个数中的）结束位置
      if (this.pageNo === this.maxPage && this.hasRemainder) {
        // 如果当前页等于最大的页数，结束位置是余数
        return this.itemsPerPage * (this.pageNo - 1) + (this.total % this.itemsPerPage)
      } else {
        return this.itemsPerPage * this.pageNo
      }
    }
  },
  methods: {
    setPage (page) { // 对外暴露带有请求页数的分页事件
      this.$emit('paginate', page)
    },
    selectPage () { // 自主选择每页查询的页数
      this.$emit('changePage', this.selectedPage)
    }
  }
}
</script>
