<template lang="pug">
div
  slot(:download="download")
    button.btn.btn-primary(type='button', @click="download") {{ btnText }}
</template>

<script>
import downloadMethods from './downloadMethods'

export default {
  name: 'download-file',
  props: {
    useBlob: { // 是否使用Blob方法
      type: Boolean,
      default: false
    },
    btnText: { // 按钮的文案
      type: String,
      default: 'Download'
    }
  },
  mixins: [downloadMethods],
  methods: {
    download () {
      this.$emit('download', this.useBlob ? this.exportFile : this.downloadFileByUrl)
    }
  }
}
</script>
