<template lang="pug">
.upload
  //- 显示已上传的图片
  .position-relative.h-100(v-if='hasUpload')
    img.img-preview.w-100.h-100(:src='img.url' alt='upload file')
    .img-delete-icon.iconfont.icon-times-circle(@click='deleteImg')
  //- 未上传图片，点击弹出选择文件上传区域
  SelectFile.h-100(v-else
    :class='{ "invalid": isInvalid }'
    :isUploading='isUploading'
    acceptFileType='.png, .jpg, .jpeg'
    :tipText='tipText'
    @set-upload-file="validateFile")
    .size-tip.mt-2.text-center(v-show="isSizeTipShown")
      | {{ 'Image Size≤' + maxSize + 'kb' }}
      br
      | {{ 'Pixel Size≤' + maxPixelSize + '*' + maxPixelSize }}
</template>

<script>
import SelectFile from './SelectFile.vue'

export default {
  name: 'img-upload',
  props: {
    img: { // img对象，{ url, id }
      type: Object,
      required: true
    },
    maxSize: { // 图片大小最大值，默认不大于500kb
      type: Number,
      default: 500
    },
    maxPixelSize: { // 图片分辨率最大值，默认不大于800*800
      type: Number,
      default: 800
    },
    tipText: String, // 提示文案
    isInvalid: { // 是否是有效的图片
      type: Boolean,
      default: false
    },
    uploadUrl: { // 上传图片的url
      type: String,
      default: '/seller/image/upload'
    },
    isSizeTipShown: { // 是否显示图片大小和图片分辨率的提示信息
      type: Boolean,
      default: true
    },
    isUploading: { // 是否正在上传
      type: Boolean,
      default: false
    }
  },
  components: {
    SelectFile
  },
  computed: {
    hasUpload() { // 是否已经上传图片
      return this.img.url !== ''
    }
  },
  methods: {
    /**
     * 校验图片大小和分辨率后执行图片上传
     * @param {object} file - 图片对象
     */
    validateFile(file) {
      // 校验图片的大小
      if (file.size > this.maxSize * 1024) { // 文件对象size属性的默认单位为字节，图片大小不合格的抛出错误
        this.$emit('size-err')
        return
      }
      // 校验图片的分辨率
      let reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = (e) => {
        // 加载图片获取真实宽高
        let img = new Image()
        img.src = e.target.result
        img.onload = () => {
          if (img.width > this.maxPixelSize || img.height > this.maxPixelSize) { // 图片分辨率不合格的抛出错误
            this.$emit('size-err')
          } else {
            this.upload(file)
          }
        }
      }
    },
    upload(file) { // 上传图片
      this.$emit('img-upload', file)
    },
    deleteImg() { // 删除图片
      this.$emit('img-delete')
    },
  }
}
</script>

<style scoped lang="scss">
.upload {
  width: 10rem;
  height: 10rem;
  .img-preview {
    border-radius: .25rem;
  }
  .invalid {
    background-color: #FCDCD8;
  }
}

.size-tip {
  font-size: 0.875rem;
  line-height: 1rem;
  color: #999;
}
</style>