import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

let routerMap = [{
  path: '/',
  name: 'home',
  component: Home
}];
// 循环添加路由
const files = require.context('../../', true, /index.js$/);
files.keys().forEach((file) => {
  let start = file.indexOf('/') + 1;
  let end = file.indexOf('/', start);
  let filename = file.substring(start, end);
  routerMap.push({
    path: '/' + filename,
    name: filename,
    component: () => import (`../../${filename}/example/App.vue`)
  })
})

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: routerMap
})
