<template lang="pug">
span
  slot(:enableDrag='enableDrag', :disableDrag='disableDrag')
    button.btn.btn-light.handle-icon.text-info(@mouseover='enableDrag', @mouseleave='disableDrag') ☰
</template>

<script>
import emitter from '../mixins/emitter'

export default {
  name: 'drag-handle',
  mixins: [emitter],
  methods: {
    enableDrag () {
      this.dispatch('draggable', 'changed-drag-handle-flag', true)
    },
    disableDrag () {
      this.dispatch('draggable', 'changed-drag-handle-flag', false)
    }
  }
}
</script>

<style scoped>
.handle-icon {
  font-size: 20px;
}
.handle-icon:hover {
  cursor: move;
}
</style>