<template lang="pug">
.wrapper
  input.form-control(type='text' :value='searchString' :class='{ "is-invalid": isNotValid }'
    @keyup.up.prevent='arrowUp'
    @keyup.down.prevent='arrowDown'
    @input='onInput'
    @keyup.enter='onEnter'
    @focus='onFocus'
    @blur='updateValue'
  )
  ul.list-group(v-show='isShown')
    li.list-group-item(v-for='(data, index) in suggestList'
      :class='{ "active": isActive(index) }'
      @click='select(data)'
    ) {{ data }}
</template>

<script>
import { debounce } from 'throttle-debounce'

export default {
  name: 'cf-auto-complete-input',
  props: [
    'dataSource', // 自动补全数据源
    'limit', // dropdown组件最多展示多少个结果
    'defaultValue', // 子组件input初始值
    'isNotValid' // 输入值是否不通过数据校验
  ],
  data () {
    return {
      isShown: false, // 是否显示下拉选择浮层
      searchString: '', // 用户输入值
      suggestion: [], // 匹配项
      selectedIndex: 0, // 默认选中匹配项的第一个
    }
  },
  computed: {
    suggestList () { // 显示指定个数的匹配项
      let len = this.suggestion.length
      if (len > 0) {
        // 匹配项超过limit个数时，只显示前几个
        let tmpVal = len > this.limit ? this.limit : len
        return this.suggestion.slice(0, tmpVal)
      } else {
        return []
      }
    },
    arrowRange () { // arrow 键上下移动范围
      return this.suggestList.length
    }
  },
  mounted () { // 回填初始值
    this.searchString = this.defaultValue
  },
  methods: {
    /**
     * 在全部可选项里进行模糊匹配，不区分大小写
    */
    findMatch () {
      if (this.searchString.length > 0) {
        let regStr = new RegExp(this.searchString, 'i')
        let tmpArry = this.dataSource.filter(ele => {
          return regStr.test(ele)
        })
        if (tmpArry.length > 0) {
          // 输入改变时，重置arrow默认选中项
          this.selectedIndex = 0
          this.suggestion = tmpArry
        } else {
          this.suggestion = []
        }
      } else { // 默认展示
        this.selectedIndex = 0
        this.suggestion = this.dataSource
      }
    },
    /**
     * 在input事件里（1）设置子组件input状态（2）debounce 查找是否有匹配项
    */
    onInput (e) {
      this.searchString = e.target.value.trim()
      this.debounceFindMatch()
    },
    /**
     * 鉴于不是网络请求，所以采用防抖，同时减少间隔时间，从而提高查询的及时性，以及最终的查询入参的准确性
    */
    debounceFindMatch: debounce (100, function () {
      this.findMatch()
    }),
    /**
     * up，down 按键事件通过改变选中的匹配项索引，实现高亮
    */
    arrowUp () {
      this.selectedIndex = (this.selectedIndex + this.arrowRange - 1) % this.arrowRange
    },
    arrowDown () {
      this.selectedIndex = (this.selectedIndex + 1) % this.arrowRange
    },
    isActive (index) {
      return index === this.selectedIndex
    },
    // enter 按键选中
    onEnter () {
      this.searchString = this.suggestList[this.selectedIndex]
      this.updateValue()
    },
    // click 点击选中
    select (data) {
      this.searchString = data
      this.updateValue()
    },
    // focus 聚焦时显示下拉补全浮层，并根据input状态显示提示项
    onFocus () {
      this.isShown = true
      this.findMatch()
    },
    /**
     * 把子组件input状态传给父组件，并关闭下拉补全浮层
     * 1.blur 输入框失焦需要等一会关闭浮层，否则无法 click 选中
     * 2.提交前设置 searchString 的值，触发了findMatch函数，因此必须通过延迟才能实现关闭浮层
    */
    updateValue () {
      this.$emit('inputReady', this.searchString)
      setTimeout(() => this.isShown = false, 300)
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  position: relative;
}
/*补充bootstarp样式*/
.list-group {
  position: absolute;
  z-index: 2;
  text-align: left;
  width: 100%;
}
.list-group-item:hover {
  cursor: pointer;
  text-decoration: none;
}
.list-group-item-heading {
  font-size: 1.5rem;
}
/*override bootstrap样式*/
.list-group-item.active,
.list-group-item:hover {
  color: #0c0c0c;
  background-color: #EDF2F5;
}
</style>
